import { fetchData } from "@/lib/api";
import { Banners } from "@/types/type";
import Image from "next/image";
import Link from "next/link";
import React from "react";

const Banner = async () => {
  let banners: Banners[] = [];

  // Helper function to extract image URL
  const getImageUrl = (image: string | { url: string; publicId: string } | undefined): string | null => {
    if (!image) return null;
    if (typeof image === "string") return image || null;
    return image.url || null;
  };

  try {
    // Disable cache to always get fresh data from database
    const data = await fetchData<Banners[]>("/banners", {
      cache: 'no-store',
      next: { revalidate: 0 }
    });
    banners = data;
  } catch (error) {
    console.error("error", error);
  }
  const imageOne = banners[0];

  if (!imageOne) {
    return null;
  }

  return (
    <div className="relative w-full h-[500px] md:h-[600px] bg-gray-100 overflow-hidden group">
      {getImageUrl(imageOne?.image) ? (
        <Image
          src={getImageUrl(imageOne?.image)!}
          alt={imageOne?.title || "banner"}
          fill
          priority
          className="object-cover object-center group-hover:scale-105 transition-transform duration-1000"
        />
      ) : (
        <div className="w-full h-full bg-linear-to-r from-pink-100 to-purple-100 flex items-center justify-center">
          <span className="text-9xl opacity-20">üõçÔ∏è</span>
        </div>
      )}

      {/* Modern Gradient Overlay */}
      <div className="absolute inset-0 bg-linear-to-r from-black/50 via-transparent to-transparent md:from-black/40"></div>

      {/* Content */}
      <div className="absolute inset-0 flex items-center">
        <div className="max-w-7xl mx-auto px-4 w-full">
          <div className="max-w-xl text-white space-y-6 fade-in slide-in-from-left-10 duration-700 animate-in">
            <p className="text-sm md:text-base font-medium tracking-[0.2em] uppercase text-pink-200">
              {imageOne?.name || "Nueva Colecci√≥n"}
            </p>
            <h2 className="text-4xl md:text-6xl font-bold leading-tight drop-shadow-lg">
              {imageOne?.title || "Descubre tu belleza √∫nica"}
            </h2>

            <div className="pt-4">
              <Link
                href={"/shop"}
                className="inline-flex items-center justify-center bg-white text-black px-8 py-4 rounded-full font-semibold hover:bg-tiendaLVSecondary hover:text-white transition-all duration-300 shadow-xl hover:shadow-2xl hover:scale-105"
              >
                Comprar Ahora
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Banner;
